import { createSlice } from '@reduxjs/toolkit';

export const totalCartPriceSlice = createSlice({
	name: 'totalCartPriceSlice',
	initialState: {
		value: 0,
	},
	reducers: {
		//   
		changePrice(state, a) {
			// state id    id price   
			if (a.payload) {
				state.value += a.payload;
			}
		},

		//   value  
		minusPrice(state, a) {
			state.value -= a.payload;
		},
	},
});
export let { changePrice, minusPrice } = totalCartPriceSlice.actions;
