import React from 'react';
import PaymentCheckButton from '../molecules/PaymentCheckButton';
import styled from 'styled-components';

const Ul = styled.ul`
	padding: 18px;
	border-top: ${(props) => props.theme.lightGray} 2px solid;
	border-bottom: ${(props) => props.theme.lightGray} 2px solid;
	margin-top: 18px;
`;

export default function PaymentList() {
	return (
		<Ul>
			<PaymentCheckButton text=" /" id="CARD" />
			<PaymentCheckButton text=" " id="DEPOSIT" />
			<PaymentCheckButton text="  " id="PHONE_PAYMENT" />
			<PaymentCheckButton text=" " id="NAVERPAY" />
			<PaymentCheckButton text=" " id="KAKAOPAY" />
		</Ul>
	);
}
