export const cartAppend = async (postFunc, formData, token, navigator) => {
	const res = await postFunc(formData, token);

	try {
		if (res) {
			if (window.confirm(' .  ?')) {
				navigator('/cart');
			} else {
				window.location.reload();
			}
		}
	} catch (error) {
		console.log(error.message);
	}
};
