import { createSlice } from '@reduxjs/toolkit';

//        
export const selectedItemSlice = createSlice({
	name: 'selectedItemSlice',
	initialState: {
		item: 0,
		fee: 0,
	},
	reducers: {
		notSelected(state, a) {
			state.item += a.payload.item;
			state.fee += a.payload.fee;
		},
	},
});

export let { notSelected } = selectedItemSlice.actions;
