const idDoubleCheck = async(id, postFunc, setConfirmtext) =>{
    const postData = {username : id};
    
    try{
        const response = await postFunc(postData);
        console.log(response);
        
        if (response){
          return setConfirmtext(" :)");
        } 
        
      }catch(error){
        if (error.response.data.FAIL_Message === 'username   :)'){
          return setConfirmtext(" .");
        } else {
          return setConfirmtext(" .");
        }
  }}

  export {idDoubleCheck};